import React, { FunctionComponent, useMemo, useState } from 'react'
import GlobalStyle from 'components/Common/GlobalStyle'
import MainNav from 'components/Common/Nav/MainNav'
import MainCarousel from 'components/Main/MainCarousel'
import Footer from 'components/Common/Footer'
import { graphql } from 'gatsby'
import { PostListItemType } from 'types/PostItem.types'
import MainContent from 'components/Main/MainContent'
import CategoryList, { CategoryListProps } from 'components/Main/CategoryList'


type IndexPageProps = {
  data: {
    allMarkdownRemark: {
      edges: PostListItemType[]
    }
  }
}


const IndexPage: FunctionComponent<IndexPageProps> = function ({
  data: {
    allMarkdownRemark: { edges }
  }
}) {
  const [selectedCategory, selectedCategoryChange] = useState('All')
  const categoryList = useMemo(
    () =>
      edges.reduce(
        (
          list: CategoryListProps['categoryList'],
          {
            node: {
              frontmatter: { categories },
            },
          }: PostListItemType,
        ) => {
          categories.forEach(category => {
            if (list[category] === undefined) list[category] = 1;
            else list[category]++;
          });

          list['All']++;

          return list;
        },
        { All: 0 },
      ),
    [],
  )

  return (
    <>
      <GlobalStyle />
      <MainNav />
      <MainCarousel />
      <CategoryList selectedCategory={selectedCategory} selectedCategoryChange={selectedCategoryChange} categoryList={categoryList} />
      <MainContent posts={edges} selectedCategory={selectedCategory} />
      <Footer />
    </>
  )
}

export default IndexPage

export const getPostList = graphql`
  query getPostList {
    allMarkdownRemark(
      sort: { order: DESC, fields: [frontmatter___date, frontmatter___title] }
    ) {
      edges {
        node {
          id
          frontmatter {
            title
            summary
            date(formatString: "YYYY.MM.DD.")
            categories
            thumbnail {
              childImageSharp {
                gatsbyImageData(width: 200, height: 270)
              }
            }
          }
        }
      }
    }
  }
`
