import styled from '@emotion/styled'
import { Link } from 'gatsby'
import React, { FunctionComponent } from 'react'
import { GatsbyImage } from 'gatsby-plugin-image'
import { PostFrontmatterType } from 'types/PostItem.types'

type PostItemProps = PostFrontmatterType & { link: string }

const PostItemMargin = styled.div`
  width: 100%;
`

const PostItemWrapper = styled(Link)`
  margin: 20px;
  display: flex;
  
`
const PostImage = styled(GatsbyImage)`
  height: 300px;
  width: 50%;
  margin-right: 20px;
  @media (min-width: 768px) {
    width: 180px;
  }
  @media (min-width: 1200px) {
    width: 160px;
  }
`
const PostContent = styled.div`
  width: 50%;
  height: 300px;
`
const CategoryItemWrap = styled.div`
  display:flex;
  overflow: hidden;
  flex-wrap: wrap;
`
const CategoryItem = styled.span`
  font-size: 12px;
  margin-right: 10px;
`
const CategoryItemTitle = styled.h4`
  font-size: 25px;
  font-weight: bold;
  margin: 5px 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-height: 200px;
`
const CategoryItemDate = styled.p`
  font-size: 15px;
  margin: 5px 0;
`
const CategoryItemSummary = styled.p`
  font-size: 15px;
  margin: 10px 0;
  line-height: 1.5;
  max-width: 200px;

  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 5; /* 라인수 */
  -webkit-box-orient: vertical;
  word-wrap:break-word; 

`

const PostItem: FunctionComponent<PostItemProps> = function ({
  title,
  date,
  categories,
  summary,
  thumbnail: {
    childImageSharp: { gatsbyImageData },
  },
  link,
}) {
  return ( 
    <PostItemMargin>
      <PostItemWrapper to={link}>
        <PostImage image={gatsbyImageData} alt="Post Item Image" />
          <PostContent>
            <CategoryItemTitle>{title}</CategoryItemTitle>
            <CategoryItemDate>{date}</CategoryItemDate>
            
            <CategoryItemSummary>{summary}</CategoryItemSummary>
            <CategoryItemWrap>
              {categories.map(category => (
                    <CategoryItem key={category}>#{category}</CategoryItem>
                  ))}
            </CategoryItemWrap>
          </PostContent>

      </PostItemWrapper>
    </PostItemMargin>
  )
}

export default PostItem