import React, { FunctionComponent, ReactNode } from 'react'
import styled from '@emotion/styled'


export type CategoryListProps = {
  selectedCategory: string
  categoryList: {
    // 프로퍼티 이름은 문자열, 프로퍼티 값은 숫자임을 나타내는 타입 표기 방법
    [key: string]: number
  }
  selectedCategoryChange: Function
}
type CategoryItemProps = {
  active: boolean;
}

type GatsbyLinkProps = {
  children: ReactNode;
  className?: string;
  onClick: React.MouseEventHandler<HTMLButtonElement>;
} & CategoryItemProps
// eslint-disable-next-line @typescript-eslint/no-unused-vars


const CategoryItem = styled.div<GatsbyLinkProps>`
  margin-right: 15px;
  padding: 5px 10px;
  border: solid 1px #d3d3d3;
  border-radius: 30px;
  font-size: 15px;
  font-weight: ${({ active }) => (active ? '800' : '400')};
  cursor: pointer;
  background-color: ${({ active }) => (active ? 'pink' : '#fff')};
  color: ${({ active }) => (active ? 'white' : 'black')};
  &:last-of-type {
    margin-right: 0;
  }
`

const CategoryItemWrap = styled.div`
  display: flex;
  margin: 10px 10px;
  overflow: auto;
  -ms-overflow-style: none;
  ::-webkit-scrollbar { 
    display:none; 
  }

`

const CategoryList: FunctionComponent<CategoryListProps> = function ({
  selectedCategory,
  categoryList,
  selectedCategoryChange
}) {

  return (
    <>
    <CategoryItemWrap>
      {Object.entries(categoryList).map(([name]) => (
        <CategoryItem active={name === selectedCategory} onClick={()=> {selectedCategoryChange(name)}} key={name} >
          {name}
        </CategoryItem>
      ))}
    </CategoryItemWrap>
        
    
    </>
  )
}

export default CategoryList